import React from "react";
import MultiCartItemAttributeComponent from "./MultiCartItemAttributeComponent";

const CrossedOutPriceWrapper = ({children, crossedOutPrice}) => {
    if (crossedOutPrice) {
        return (
            <div>
                <div key={"price"} className="u-right u-w-100">
                    {children}
                </div>
                <del key={"crossedOutPrice"} className="h4 u-font-small g-gray5-c u-inline-block">
                    {crossedOutPrice}
                </del>
            </div>
        )
    }
    return (children);
};

export default class MultiCartItemDetailComponent extends MultiCartItemAttributeComponent {
    constructor (props) {
        super(props);
    }

    render() {
        return (
            <tr className={!this.props.hideBorder && "u-border-top"}>

            <td className={(!this.props.removeDownPadding ? "u-padding-l" : "") + (!this.props.removeTopPadding ? " u-padding-top-l" : "") + (!this.props.removeRightPadding ? " u-padding-right-l" : "")} colSpan={this.props.priceless && '3'}>
                {this.props.title ? (<span className={this.props.captionClassName}>
                    {this.props.title}
                </span>) : this.props.children}
            </td>
            { !this.props.priceless &&
                <td className="l-col-2 u-padding-l u-padding-top-l u-padding-right u-text-right">
                    <span className="u-acc-hide">Opłaty na start</span>
                    <CrossedOutPriceWrapper crossedOutPrice={this.props.oneTimePrice.crossedOut}>
                        { this.props.oneTimePrice ? this.renderPriceDesktop(this.props.oneTimePrice) : this.renderVoidDesktop() }
                    </CrossedOutPriceWrapper>
                </td>
            }
            { !this.props.priceless &&
                <td className="l-col-2 u-padding-l u-padding-top-l u-padding-left u-text-right">
                    <span className="u-acc-hide">Opłaty miesięczne</span>
                    { this.props.monthlyPrices && this.props.monthlyPrices[0] ?
                        this.renderPriceDesktop(this.props.monthlyPrices[0],null," u-left") : this.renderVoidDesktop() }
                </td>
            }
            </tr>
        )
    }

}